@font-face {
    font-family: 'hackregular';
    src: local("Hack"), url("/static/fonts/Hack-Regular-webfont.eot");
    src: local("Hack"), url("/static/fonts/Hack-Regular-webfont.eot?#iefix") format("embedded-opentype"),
         local("Hack"), url("/static/fonts/Hack-Regular-webfont.woff2") format("woff2"),
         local("Hack"), url("/static/fonts/Hack-Regular-webfont.woff") format("woff"),
         local("Hack"), url("/static/fonts/Hack-Regular-webfont.ttf") format("truetype"),
         local("Hack"), url("/static/fonts/Hack-Regular-webfont.svg#hackregular") format("svg");
    font-weight: normal;
    font-style: normal;
}

