html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

progress {
  vertical-align: baseline;
}

template,
[hidden] {
  display: none;
}

a {
  -webkit-text-decoration-skip: objects;
  background-color: transparent;
}

a:active,
a:hover {
  outline-width: 0;
}

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

dfn {
  font-style: italic;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

mark {
  color: #000;
  background-color: #ff0;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font: inherit;
}

optgroup {
  font-weight: bold;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .625em .75em;
  margin: 0 2px;
  border: 1px solid #c0c0c0;
}

legend {
  box-sizing: border-box;
  display: table;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-input-placeholder {
  color: inherit;
  opacity: .54;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

img {
  box-sizing: content-box;
}

.container {
  max-width: 1020px;
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  margin: auto;
}

.grid {
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: -0.75rem;
  margin-left: -0.75rem;
}

.grid-item {
  padding-right: 0.75rem;
  padding-left: 0.75rem;
}

.grid-left {
  justify-content: flext-start;
}

.grid-center {
  justify-content: center;
}

.grid-right {
  justify-content: flex-end;
}

.grid-top {
  align-items: flex-start;
}

.grid-middle {
  align-items: center;
}

.grid-bottom {
  align-items: flex-end;
}

.grid-reverse {
  flex-direction: row-reverse;
}

.grid-stack {
  flex-direction: column;
}

.grid-stack.grid-reverse {
  flex-direction: column-reverse;
}

.grid-stack > .grid-item {
  flex-basis: auto;
}

.grid-flat {
  margin-right: 0;
  margin-left: 0;
}

.grid-flat > .grid-item {
  padding-right: 0;
  padding-left: 0;
}

.grid-pair > .grid-item {
  text-align: left;
}

.grid-pair > .grid-item + .grid-item {
  text-align: right;
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  .compact-grid-reverse {
    flex-direction: row-reverse;
  }
  .compact-grid-stack {
    flex-direction: column;
  }
  .compact-grid-stack.compact-grid-reverse {
    flex-direction: column-reverse;
  }
  .compact-grid-stack > .grid-item {
    flex-basis: auto;
  }
}

@media screen and (min-width: 1000px) {
  .regular-grid-reverse {
    flex-direction: row-reverse;
  }
  .regular-grid-stack {
    flex-direction: column;
  }
  .regular-grid-stack.regular-grid-reverse {
    flex-direction: column-reverse;
  }
  .regular-grid-stack > .grid-item {
    flex-basis: auto;
  }
}

