.media-reverse {
  direction: rtl;
}

.media-reverse > *:not(.media-body) {
  margin-right: 0;
  margin-left: 1.5rem;
}

.media-reverse[class~="media-1/4"] > *:not(.media-body) {
  margin-left: 0.375rem;
}

.media-reverse[class~="media-1/3"] > *:not(.media-body) {
  margin-left: 0.5rem;
}

.media-reverse[class~="media-1/2"] > *:not(.media-body) {
  margin-left: 0.75rem;
}

.media-reverse[class~="media-2/1"] > *:not(.media-body) {
  margin-left: 3rem;
}

.media-reverse[class~="media-3/1"] > *:not(.media-body) {
  margin-left: 4.5rem;
}

.media-reverse[class~="media-4/1"] > *:not(.media-body) {
  margin-left: 6rem;
}

.media-reverse.media-flat > *:not(.media-body) {
  margin-left: 0;
}

.sticky-footer {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.sticky-footer > *:first-child {
  flex: 1;
}

