body {
  font-family: "opensans";
}

h1, h2, h3, h4, h5, h6 {
  font-family: "roboto";
}

blockquote {
  font-family: "hackregular";
  font-size: 0.7em;
}

img {
  max-width: 100%;
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  img {
    width: 100%;
  }
}

.nostyle {
  color: inherit;
  text-decoration: none;
}

.brands a {
  margin-bottom: 1rem;
}

@media screen and (min-width: 1000px) {
  .brands a {
    margin-left: 1rem;
  }
}

.brands a picture img {
  width: 135px;
  height: auto;
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  .brands a picture img {
    width: 100%;
    height: auto;
  }
}

.gallery-brands {
  align-items: stretch;
  clear: both;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.gallery-brands a {
  align-self: stretch;
  display: block;
  flex: 2 2 10em;
  margin: 0.4em;
  padding: 2em 2em 3em 2em;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}

@media screen and (min-width: 1000px) {
  .article-header {
    text-align: center;
  }
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  .article-header h1 {
    font-size: 1.1rem;
  }
}

article.blog-card {
  background: #f6f6f6;
  position: relative;
  padding: 0;
  margin: 0 1rem;
  border-radius: 2%;
}

article.blog-card a {
  color: #808080;
  text-decoration: none;
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  article.blog-card {
    margin-bottom: 1rem !important;
  }
}

article.blog-card [class*="icon"] {
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

article.blog-card .card-header {
  position: relative;
  color: #f6f6f6;
}

article.blog-card .card-header h4 {
  position: absolute;
  background: #006da6;
  width: 100%;
  bottom: -1.5rem;
}

article.blog-card .card-footer {
  position: absolute;
  bottom: .1rem;
}

article.blog-card .card-footer .icon-clock2 {
  width: 0.7rem;
  height: 0.7rem;
  display: inline;
  fill: #808080;
}

article.blog-card:hover {
  opacity: 0.5;
}

aside.post .linklist a:before {
  content: "- ";
}

aside.post .linklist a {
  display: block;
  color: #73cfff;
  margin-top: 0.5rem;
}

aside.post .linklist a:hover {
  transition: color 500ms linear 100ms;
  color: white;
  text-decoration: underline;
}

aside.post .gallery img {
  margin-top: 0.5rem;
}

header.main {
  max-width: 100%;
}

header.main img {
  width: 115px;
  height: 110px;
  padding: 1rem 3rem;
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  header.main img {
    display: block;
    margin: 0 auto;
    padding: 1rem 0;
  }
}

main {
  max-width: 100%;
  padding: 0;
  margin: 0;
}

@media screen and (min-width: 320px) and (max-width: 999px) {
  nav {
    text-align: center;
    padding: 2rem 0;
  }
  nav a {
    padding-right: 1rem;
    text-decoration: none;
  }
  nav a:last-child {
    padding-right: 0;
  }
  nav a:hover {
    color: #73cfff;
  }
}

@media screen and (min-width: 1000px) {
  nav {
    position: absolute;
    top: 3rem;
    right: 3rem;
  }
  nav a {
    font-size: 1.4rem;
    text-decoration: none;
    padding-left: 2rem;
  }
  nav a:first-child {
    padding-left: 0;
  }
  nav a:hover {
    color: #73cfff;
  }
}

nav .icon-info-circle:hover,
nav .icon-paragraph-justify:hover,
nav .icon-twitter:hover,
nav .icon-github:hover {
  fill: #73cfff;
}

.menu-active span {
  color: #40bdff;
  text-decoration: underline;
}

.menu-active .icon {
  stroke: #40bdff;
  fill: #40bdff;
}

section {
  width: 100%;
  max-width: 100%;
}

.cover {
  position: relative;
  color: #f6f6f6;
}

.cover img {
  background-size: cover;
  background-position: center center;
  width: 100%;
  max-height: 500px;
}

.cover .article-header {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.med a {
  color: #73cfff;
  text-decoration: none;
}

.med a:hover {
  transition: color 500ms linear 100ms;
  color: white;
}

.med div a {
  padding-right: 1rem;
}

.film a:nth-child(even),
.serie a:nth-child(even),
.books a:nth-child(even) {
  color: #0dacff;
  font-size: 1.2rem;
  text-decoration: none;
  padding-right: 1rem;
}

.film a:nth-child(even):hover,
.serie a:nth-child(even):hover,
.books a:nth-child(even):hover {
  transition: color 500ms linear 100ms;
  color: #73cfff;
  text-decoration: underline;
}

.film a:nth-child(odd),
.serie a:nth-child(odd),
.books a:nth-child(odd) {
  color: #73cfff;
  font-size: 1.4rem;
  text-decoration: none;
  padding-right: 1rem;
}

.film a:nth-child(odd):hover,
.serie a:nth-child(odd):hover,
.books a:nth-child(odd):hover {
  transition: color 500ms linear 100ms;
  color: white;
  text-decoration: underline dotted;
}

.pagination {
  padding-top: 1rem;
  text-align: center;
}

@media screen and (min-width: 1000px) {
  .pagination {
    margin: 0 1em;
  }
}

.pagination a {
  color: #a19f9e;
  padding-right: 1em;
  text-decoration: none;
  font-family: "hack";
  color: #73cfff;
  border: thin solid #73cfff;
}

.pagination a:hover {
  color: #dddddd;
  transition: background-color 500ms linear 100ms;
  background-color: black;
}

.pagination .pagination__current {
  color: #c3c3c3;
  transition: background-color 500ms linear 100ms;
  background-color: black;
}

.tags {
  text-align: center;
}

@media screen and (min-width: 1000px) {
  .tags {
    margin: 0 1em;
  }
}

.tags a {
  color: #a19f9e;
  padding-right: 1em;
  text-decoration: none;
  font-family: "hack";
}

.tags a:last-child {
  padding-right: 0;
}

.tags a:hover {
  color: #dddddd;
  transition: background-color 500ms linear 100ms;
  background-color: black;
}

@media screen and (min-width: 1000px) {
  .tags a {
    display: block;
  }
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: #006da6;
  fill: #006da6;
}

.quote-box {
  border-radius: 3px;
  position: relative;
  margin: 8% auto auto auto;
  padding: 2rem 3rem;
  overflow: hidden;
}

.quote-box button {
  position: relative;
  top: 0;
  right: 0;
}

.quote-box .btn {
  cursor: pointer;
  opacity: 1;
  background: #4c7fa7;
  color: #fff;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  border-radius: 3px;
  border: none;
  outline: none;
}

.quote-box .btn:hover {
  opacity: 0.8;
}

#quote {
  overflow: hidden;
  margin-bottom: 20px;
  opacity: 1;
  transition: all 0.8s cubic-bezier(0.44, 1.13, 0.58, 1);
}

.back-to-top {
  background: none;
  margin: 0;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 70px;
  height: 70px;
  z-index: 100;
  display: none;
  text-decoration: none;
  opacity: 0.6;
}

#youtube {
  margin: 1rem 0;
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
}

#youtube iframe, #youtube object, #youtube embed {
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#cookiedingsbums {
  outline: none;
  text-align: center;
  border-top: thin solid #cccbca;
  background: #1e1916;
  position: fixed;
  bottom: 0;
  z-index: 100000;
  width: 100%;
  line-height: 1.4rem;
}

#cookiedingsbums a {
  text-decoration: none;
}

#cookiedingsbums a:hover {
  text-decoration: underline;
  color: #ff4136;
}

#cookiedingsbums div {
  padding: 1rem;
  padding-right: 3rem;
}

#cookiedingsbums #cookiedingsbumsCloser {
  color: #cccbca;
  position: absolute;
  right: 0.5rem;
  text-decoration: none;
  top: 1rem;
  cursor: pointer;
}

#cookiedingsbums #cookiedingsbumsCloser:hover {
  color: white;
}
