h1, h2, h3, h4, h5, h6,
p, ul, ol, dl, dd, hr,
blockquote, pre, table, th, td,
form, fieldset, caption, legend {
  padding: 0;
  margin: 0;
}

p, ul, ol, dl, hr,
blockquote, pre, table, form {
  margin-bottom: $spacing-unit;
}

ul ul,
ol ol,
ul ol,
ol ul {
  margin-top: 0;
  margin-bottom: 0;
}

html {
  overflow-y: scroll;
}

body {
  font-family: $font-family-base;
  font-weight: $font-weight-normal;
  color: $color-text;
  background-color: $color-body;
}
